.opry-header {
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  transition: all 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);
  box-shadow: 0 5px 10px 0 rgba(50, 50, 50, 0.08);
}
.opry-header .site-title {
  font-size: 26px;
  font-weight: 700;
}
.opry-header.fixed-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.opry-header__sub-menu {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  gap: 30px;
}
.opry-header__sub-menu .header-search-icon i {
  font-family: "FontAwesome";
  color: #fff;
  cursor: pointer;
}
.opry-header__sub-menu .header-our-brand {
  position: relative;
}
.opry-header__sub-menu .header-our-brand .header-our-brand-title {
  position: relative;
  padding: 0px 0px 0px 10px;
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #fff;
  letter-spacing: 1px;
  line-height: 16px;
  z-index: 2;
  cursor: pointer;
}
.opry-header__sub-menu .header-our-brand .header-our-brand-title::before {
  transition: all 0.2s linear;
  content: "";
  position: absolute;
  bottom: -4px;
  left: 10px;
  display: block;
  width: 0;
  border-bottom: 2px solid rgba(0, 0, 0, 0);
}
.opry-header__sub-menu .header-our-brand .brand-logo-list {
  display: none;
  position: absolute;
  top: 10px;
  left: 0px;
  padding: 20px 0;
  z-index: 1;
  width: 100%;
  list-style: none;
}
.opry-header__sub-menu .header-our-brand .brand-logo-list.open-list {
  display: block;
}
.opry-header__sub-menu .header-our-brand .brand-logo-list::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0;
  display: block;
  width: 100%;
  height: calc(100% + 53px);
  background-image: linear-gradient(135deg, #3c1c3d 0%, #3271f5 100%);
}
.opry-header__sub-menu .header-our-brand .brand-logo-list li {
  display: block;
  text-align: center;
}
.opry-header__sub-menu .header-our-brand .brand-logo-list li a {
  zoom: 1;
  letter-spacing: 2px;
  opacity: 0.5;
  filter: alpha(opacity=50);
  text-align: center;
  display: block;
  padding: 12px 0px;
  border: none;
  color: #363534;
  text-decoration: none;
  cursor: pointer;
  transition: all 200ms linear;
}
.opry-header__sub-menu .header-our-brand .brand-logo-list li a:hover {
  zoom: 1;
  opacity: 1;
}

@media (min-width: 768px) {
  .opry-menu ul {
    position: relative;
    text-align: right;
    margin-right: 25px;
  }
  .opry-menu ul li {
    position: relative;
    display: inline-block;
    margin-right: 30px;
  }
  .opry-menu ul li a {
    letter-spacing: 2px;
    position: relative;
    z-index: 999;
    color: #fff;
  }
