    background-size: 100% 2px;
  }
  .opry-menu ul li .children,
  .opry-menu ul li .sub-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    padding: 5rem 0;
    margin: 0.125rem 0 0;
    text-align: left;
    background-clip: padding-box;
    margin-top: 0;
    min-width: 15rem;
  }
  .opry-menu ul li .children li,
  .opry-menu ul li .sub-menu li {
    margin-right: 0;
    display: block;
    padding: 10px;
  }
  .opry-menu ul li .children li:last-child,
  .opry-menu ul li .sub-menu li:last-child {
    border-bottom: none;
  }
  .opry-menu ul li .children li .children,
  .opry-menu ul li .children li .sub-menu,
  .opry-menu ul li .sub-menu li .children,
  .opry-menu ul li .sub-menu li .sub-menu {
    display: none;
  }
  .opry-menu ul li .children li:hover .children,
  .opry-menu ul li .children li:hover .sub-menu,
  .opry-menu ul li .sub-menu li:hover .children,
  .opry-menu ul li .sub-menu li:hover .sub-menu {
    display: block;
    right: 100%;
    left: initial;
    top: -3px;
  }
  .opry-menu ul li:hover .children,
  .opry-menu ul li:hover .sub-menu {
    display: inline-block;
  }
  .opry-menu ul li:last-child {
    margin-right: 0;
  }
  .opry-menu ul li:nth-child(3) .children,
  .opry-menu ul li:nth-child(3) .sub-menu, .opry-menu ul li:nth-child(4) .children,
  .opry-menu ul li:nth-child(4) .sub-menu, .opry-menu ul li:nth-child(5) .children,
  .opry-menu ul li:nth-child(5) .sub-menu, .opry-menu ul li:nth-child(6) .children,
  .opry-menu ul li:nth-child(6) .sub-menu {
    left: inherit;
    right: 0;
  }
}
@media (max-width: 767px) {
  .opry-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    left: -100%;
    z-index: 9;
    padding-top: 75px;
    background-color: #ffffff;
    overflow: auto;
    transition-property: left;
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  }
  .opry-menu ul > li {
    position: relative;
    display: inline-block;
    width: 100%;
    border-top: 1px solid #e1e1e5;
  }
  .opry-menu ul > li a {
    font-size: 15px;
    font-weight: 500;
    line-height: 26px;
    margin: 0;
    padding: 13px;
    display: inline-block;
    width: calc(100% - 60px);
  }
  .opry-menu ul > li.menu-item-has-children::after {
    content: "+";
    font-family: dashicons;
    position: absolute;
    top: 11px;
    right: 10px;
    font-size: 18px;
    width: 20px;
    text-align: center;
  }
  .opry-menu ul > li.menu-item-has-children.active-sub::after {
    content: "-";
  }
  .opry-menu ul > li.menu-item-has-children .sub-menu,
  .opry-menu ul > li.menu-item-has-children .children {
    display: none;
    border-top: 1px solid #e1e1e5;
    background-color: #f3f3f3;
  }
  .opry-menu ul > li.menu-item-has-children .sub-menu .menu-item-has-children::after,
  .opry-menu ul > li.menu-item-has-children .children .menu-item-has-children::after {
    content: none;
  }
  .opry-menu ul > li.menu-item-has-children .sub-menu .menu-item-has-children ul li a,
  .opry-menu ul > li.menu-item-has-children .children .menu-item-has-children ul li a {
    padding-left: 30px;
  }
  .opry-menu ul > li:first-child {
    border-top: none;
  }
}

